@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,200;0,300;0,400;0,500;0,600;1,200;1,300;1,400;1,500;1,600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700;1,900&display=swap);
:root {
  --sg-blurple: #5033e1;
  --sg-light-gray: #f4f7fb;
  --sg-vermillion: #ff5543;
  --sg-violet: #a112ff;
  --sg-mint: #8fedcf;
  --sg-black: #000000;
  --sg-btn-hover: #3c26a5;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.btn {
  border: 2px solid #5033e1;
  border-radius: 3px;
  background-color: #5033e1;
  color: #ffffff;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 40px;
  font-family: "Source Sans Pro", Arial, Helvetica, sans-serif;
  font-weight: 600;
  font-size: 1.0rem;
  margin-top: 10px;
  padding-right: 22px;
  padding-left: 22px;
  justify-self: flex-end;
  transition: all .2s ease-in;
  cursor: pointer;
}

.btn:hover {
  background-color: #3c26a5;
  background-color: var(--sg-btn-hover);
  color: white;
}

.btn:active {
  background-color: #ffffff;
  color: #5033e1;
}

button.btn:disabled,
button.btn[disabled] {
  border-color: #f4f7fb;
  border-color: var(--sg-light-gray);
  background-color: #f4f7fb;
  background-color: var(--sg-light-gray);
  color: lightgray;
  font-weight: regular;
  cursor: default;
}
.deployment-container {
    width: 50%;
    text-align: left;
    max-width: 500px;
}

.deployment-type {
    width: 100%;
}

.dropdown-menu {
    border: 1px solid lightgray;
    border-radius: 2px;
    font-family: "Source Sans Pro", monospace;
    font-size: 1rem;
    color: rgb(47, 47, 47);
    outline: #00CBEC;
    padding: 5px;
    max-width: 350px;
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
}

.text-input {
    border: 1px solid lightgray;
    border-radius: 4px;
    font-family: "Source Code Pro", monospace;
    font-size: 1rem;
    color: rgb(47, 47, 47);
    outline: #00cbec;
    padding: 5px;
    padding-left: 10px;
    margin-top: 3px;
    height: 25px;
    max-width: 300px;
    min-width: 200px;
}

.namespace-check {
    margin-top: 10px;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    font-family: "Source Sans Pro", Arial, Helvetica, sans-serif;
    font-size: .8rem;
}

.namespace-label {
    margin-left: 4px;
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
}

.namespace-input {
    margin-top: 10px;
    width: 100%;
}
.actions-container {
    padding-top: 10px;
    width: 50%;
    max-width: 500px;
    text-align: left;
}

.action-option {
    margin-top: 10px;
}

.generated-uri-container {
    width: 65%;
    max-width: 1010px;
    padding-top: 25px;
    margin: auto;
    display: flex;
    flex-flow: column nowrap;
}

.generated-uri {
    width: 100%;
    max-width: 1000px;
    border: 1px solid lightgray;
    border-radius: 4px;
    font-family: "Source Code Pro",
        monospace;
    font-size: 1rem;
    color: rgb(70, 70, 70);
    outline: #00cbec;
    padding-top: 10px;
    padding-bottom: 10px;
    margin-top: 3px;
    padding-left: 10px;
}

input.generated-uri[type="text"] {
    color: gray;
    text-shadow: 0 0 0 #fff;
}

input.generated-uri[type="text"]:focus {
    outline: none;
}

.copied-url-container {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
}

.copied-message {
    color: #ED2E20;
    font-family: "Source Sans Pro", Arial, Helvetica, sans-serif;
    font-size: 1.0;
    font-weight: light;
    opacity: 0;
    width: 500px;
    text-align: left;
    transition-duration: .4s;
}

.copied-message.show {
    opacity: 1;
}

button.btn.w-margin:disabled,
button.btn.w-margin[disabled] {
    margin-left: 10px;
}

.btn.w-margin {
    margin-left: 10px;
}
.App {
  text-align: center;
  margin-top: 100px;
}

.container {
  width: 65%;
  max-width: 1020px;
  margin: auto;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
}

.actions {
  display: flex;
  flex-flow: column nowrap;
  width: 45%;
  min-width: 400px;
  min-height: 360px;
  margin-left: auto;
  margin-right: 0px;
}

.line-break {
  height: 275px;
  min-height: 275px;
  border-left: 1px solid lightgray;
}

.descriptions-container {
  width: 50%;
  max-width: 500px;
  min-height: 360px;
  min-width: 400px;
}

.logo {
  width: 300px;
}

.subtitle {
  font-family: "Source Code Pro", monospace;
  font-weight: 400;
  font-size: 1.5rem;
}

.section {
  font-family: "Source Sans Pro", Arial, Helvetica, sans-serif;
  font-weight: 600;
  text-align: left;
}

.section.right {
  padding-left: 44px;
}

.deploy-container {
  width: 65%;
  margin-left: auto;
}
.desc-list {
    list-style: none;
    text-align: left;
    font-family: "Source Code Pro", monospace;
    font-size: .7rem;
}

.desc-list.placeholder {
    margin-left: 46px;
    font-size: .8rem;
}

.description {
    list-style: none;
    padding-top: 8px;
    padding-bottom: 8px;
    padding-right: 5px;
}

.instruction {
    font-weight: normal;
}

.row {
    padding-top: 2px;
    padding-bottom: 2px;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-between;
    font-size: .8rem;
    cursor: pointer;
    border-radius: 2px;
    padding-left: 5px;
    padding-right: 5px;
    max-width: 340px;
}

.row:hover {
    background-color: rgb(248, 248, 248);
}

.row.expanded {
    background-color: rgb(248, 248, 248);
}

.expand {
    border: none;
    background-color: transparent;
    font-family: "Source Code Pro", monospace;
    font-size: .7rem;
    cursor: pointer;
    transition: .2s;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    position: relative;
    left: 40px;
    opacity: 0;
    transition: all .3s ease-in-out;
}

.expand.active {
    opacity: 1;
    transform: translateX(-40px);
}

.info-key {
    font-weight: bold;
}

.rotated {
    transform: rotate(-90deg);
    transition-duration: .2s;
}

.not-rotated {
    transition-duration: .2s;
}

.command-info {
    background-color: rgb(248, 248, 248);
    font-family: "Source Code Pro", Arial, Helvetica, sans-serif;
    font-size: .7rem;
    padding-bottom: 10px;
    width: 350px;
    animation: fadeIN .2s linear;
    opacity: 1;
}

@keyframes fadeIN {

    0% {
        opacity: 0;
        -webkit-transform: translateY(-15px);
    }

    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
    }
}
.or-container {
    width: 65%;
    margin: auto;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: center;
}

.or {
    font-family: "Source Sans Pro", Arial, Helvetica, sans-serif;
    font-size: 1.3rem;
    font-weight: regular;
    width: 20px;
    width: 58px;
}

.dash {
    border-bottom: 1px solid black;
    height: 0px;
    width: 100px;
}
button.copy-button:disabled,
button.copy-button[disabled] {
    border: 2px solid #F4F7Fb;
    border-radius: 3px;
    background-color: #ffffff;
    color: lightgray;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: 40px;
    font-family: "Source Sans Pro", Arial, Helvetica, sans-serif;
    font-weight: regular;
    font-size: 1.0rem;
    padding-right: 22px;
    padding-left: 22px;
}

.copy-button {
    border: 2px solid #5033e1;
    border-radius: 3px;
    background-color: #ffffff;
    color: #5033e1;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: 40px;
    font-family: "Source Sans Pro", Arial, Helvetica, sans-serif;
    font-weight: 600;
    font-size: 1.0rem;
    padding-right: 22px;
    padding-left: 22px;
}

.copy-button:hover {
    background-color: #5033e1;
    color: white;
    cursor: pointer;
    padding-right: 22px;
    padding-left: 22px;
}
.generated-command-container {
    padding-top: 45px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    min-height: 100px;
    max-width: 1000px;
    min-width: 1000px;
    margin: auto;
}

.generated-command {
    font-family: "Source Code Pro", monospace;
    font-weight: normal;
    font-size: 1.2rem;
    background-color: var(--sg-black);
    padding-top: 25px;
    padding-bottom: 25px;
    color: var(--sg-mint);
    border-radius: 6px;
}

.btns-container {
    width: 100%;
    max-width: 1000px;
    margin: auto;
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;

}
